import React, { useEffect, useState } from 'react';
import './Home.css'


function Home(props) {
    const [lastEpisodeTitle, setLastEpisodeTitle] = useState("");
    const [newEpisodeNumber, setNewEpisodeNumber] = useState("");
    const [lastEpisodeURL, setLastEpisodeURL] = useState("");

    async function getRSSdata() {
        const waypointRSS = "https://rss.acast.com/vicegamingsnewpodcast";

        let response = await fetch(waypointRSS);
        if (!response.ok) {
            throw new Error('HTTP Error!');
        }

        let rssText = await response.text();
        let parser = new DOMParser();
        let dom = parser.parseFromString(rssText, 'application/xml');
        let items = dom.querySelectorAll('item');
        let lastTitle = items[0].querySelectorAll('title')[0].textContent;
        let lastURL = items[0].childNodes[5].textContent
        console.log(lastURL);

        setLastEpisodeTitle(lastTitle);
        setLastEpisodeURL("https://play.acast.com/s/vicegamingsnewpodcast/" + lastURL);
        const regex = /(\d+)/gm;
        const lastNumber = parseInt(lastTitle.match(regex)[0]);


        let nextNumber = lastNumber + 1;

        console.log("hit")
        setNewEpisodeNumber(nextNumber.toString());
    }


    getRSSdata();

    return (
        <div id="text">
            <p>The next episode of Waypoint Radio should be number...</p>
            <div id="newNumber">
               {newEpisodeNumber}
            </div>
            <p>The last episode was: <em><a href={lastEpisodeURL}>{lastEpisodeTitle}</a></em></p>
        </div>

    );
}

export default Home;